*,
*::before,
*::after {
    box-sizing: border-box;
}

@font-face {
    font-family: 'PrimaryFont';
    src: url('../font/gill-sans-nova_book.otf') format('truetype');
}

@font-face {
    font-family: 'PrimaryFont';
    font-weight: 400;
    src: url('../font/gill-sans-nova_book.otf') format('truetype');
}

@font-face {
    font-family: 'PrimaryFont';
    font-weight: 600;
    src: url('../font/gill-sans-nova_semibold.otf') format('truetype');
}

html {
    display: grid;
    place-content: center;
    width: 100%;
    height: 100%;
    background: black;

    font-family: 'PrimaryFont', 'Gill Sans', 'Gill Sans MT', 'Calibri',
        'Noto Sans', 'Noto Sans JP', 'sans-serif';
}

body {
    width: 1024px;
    height: 768px;
    border-radius: 16px;
    overflow: hidden;
    clip-path: inset(0px round 16px);
    font-family: 'PrimaryFont', 'Gill Sans', 'Gill Sans MT', 'Calibri',
    'Noto Sans', 'Noto Sans JP', 'sans-serif';
}

button {
    border: none;
    font-family: 'PrimaryFont', 'Gill Sans', 'Gill Sans MT', 'Calibri',
    'Noto Sans', 'Noto Sans JP', 'sans-serif';
    line-height: 0;
    padding-bottom: .4rem;
}
