body {
    display: grid;
    grid-template-columns: 1fr 634px;
}

#root {
    display: grid;
    grid-template-columns: 1fr 634px;
}

.control-area {
    height: 768px;
    width: 390px;
    background: linear-gradient(
        221.96deg,
        #313e49 0%,
        #171d23 56.25%,
        #080a0c 100%
    );

    padding: 48px;

    display: flex;
    flex-direction: column;
    gap: 16px;

    .title-area {
        height: 96px;
    }

    .title-object {
        height: 48px;
        margin: 0;
        display: grid;
        place-content: center;
        border-bottom: hsla(0 0% 100% / 0.12) solid 2px;
    }

    .bean-options-area {
        display: flex;
        gap: 8px;
        width: 100%;

        .coffee-option {
            display: flex;
            flex-direction: column;
            flex: 1;

            .title {
                color:  $TEXT_SECONDARY;
                height: 24px;
                margin-bottom: 4px;
                // padding: 0 16px
                // TODO: Create padding title for coffee/espresso
            }

            .align-right {
                text-align: right;
            }
        }

        .image-button {
         //   background: hsla(0 0% 100% / 0.12);
            background: none;
            border-radius: 16px;
            height: 56px;
            flex-grow: 1;
            flex-shrink: 1;
            border-radius: 8px;

            cursor: pointer;


            display: flex;
            flex-direction: column;
            gap: 8px;

            transition: background 1s;

            padding: 0;

            h5 {
                font-weight: 600;
                text-align: center;
                color: $ROSE_64;
                place-self: center;

                transition: color 1s;
            }

            // padding: 16px;

            width: 100%;

            .image-mask {
                display: grid;
                width: 100%;
                height: 100%;
                overflow: hidden;
                border-radius: 8px;

                @include ELEVATION_L;

                img {
                    width: 100%;
                    height: 100%;
                    object-position: center;
                    object-fit: cover;
                }

                &.bean-image {
                    cursor: pointer;
                    background: rgba(255, 255, 255, 0.12);

                    img {
                        height: 56px;
                        object-fit: contain;
                    }

                    &.selected {
                        background-color: $ROSE_64;
                    }
                }

                &.coffee-selection {
                    cursor: pointer;

                    &.selected {
                        outline: 3px solid $ROSE_64;
                    }
                }
            }

            &:hover {
                background: hsla(0 0% 100% / 0.24);

                h5 {
                    color: lighten($ROSE_64, 10%);
                }
            }

            &.selected {
                outline: 3px solid $ROSE_64;
            }
        }
    }
}

.preview-area {
    height: 768px;
    display: grid;
    grid-template-areas: 'stack';
    place-items: center;

    > * {
        // The following css variables have no effect unless
        // you'd like to make alterations to the specific properties within
        // the application for a specific element
        grid-area: stack;
    }

    .primary-button {
        align-self: end;
        z-index: 100;

        animation: ANM_FADEIN_BOTTOM 0.75s ease both;

        margin-bottom: 3rem;

        width: 392px;
        height: 54px;

        /* Default/color/Cactus/59 */
        background: #909E8F;
        box-shadow: 174px 146px 63px rgba(0, 5, 0, 0.01), 111px 93px 58px rgba(0, 5, 0, 0.07), 63px 52px 49px rgba(0, 5, 0, 0.24), 28px 23px 36px rgba(0, 5, 0, 0.41), 7px 6px 20px rgba(0, 5, 0, 0.47), 0px 0px 0px rgba(0, 5, 0, 0.48);
        border-radius: 8px;

        color: white;

        &:hover {
            background: #677066
        }

        &:active {
            background: #444b43
        }
    }
}


.fade-in {
    animation: ANM_FADEIN 1s ease both;
    animation-timing-function: cubic-bezier(.23,.07,.1,1.57);
}

.speedup-s {
    animation-duration: .3s;
}

.speedup-m {
    animation-duration: .3s;
}

.speedup-l {
    animation-duration: .45s;
}

@keyframes ANM_FADEIN {
    0% {
        opacity: 0;
        transform: translateY(-2rem);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes ANM_FADEIN_BOTTOM {
    0% {
        opacity: 0;
        transform: translateY(3rem);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}



.delay-15 {
    animation-delay: .15s;
}
.delay-30 {
    animation-delay: .3s;
}
.delay-45 {
    animation-delay: .45s;
}
.delay-60 {
    animation-delay: .6s;
}

.delay-70 {
    animation-delay: .7s;
}

.delay-85 {
    animation-delay: .85s;
}
.delay-100 {
    animation-delay: 1s;
}
.delay-115 {
    animation-delay: 1.15s;
}
.delay-130 {
    animation-delay: 1.3s;
}